@charset "utf-8";

/* Variable  */
/*///////////////////////////////////////////////////////////////////////*/
// Options available: "Helvetica Neue", Helvetica, Arial, sans-serif, Ubuntu Mono;
$base-font-family: 'Times New Roman';
$headline-font-family: "Orbitron Bold";
$nav-font-family: "Orbitron Black";
$bulletin-font-family: "Orbitron Medium";
$nav-content-font-family: "Orbitron Light";
$base-font-size:   16px;
$base-font-weight: 400;
$small-font-size:  $base-font-size * 0.875;
$large-font-size:  $base-font-size * 1.5;
$huge-font-size:  $base-font-size * 2;
$base-line-height: 1.5;

$spacing-unit:     30px;

/* Candidate colors:  #1eff03, #1ec503, #aaff43  */
// $text-color:  #1eff03;
$text-color:  #000000;
$interact-color: #ff0000;
$title-color: #abc0ca;
// $title-color: #ffffff;
$link-color: #03a4fb;

$background-color: #ffffff;
$brand-color:      #2a7ae2;

$grey-color:       #828282;
$grey-color-light: lighten($grey-color, 40%);
$grey-color-dark:  darken($grey-color, 25%);

/* General */
/*///////////////////////////////////////////////////////////////////////*/
a {
    color: $link-color;
    text-decoration: none;
}
a:hover {
    color:#03a4fb !important;
    text-decoration: none !important;
    border-bottom: 1px dotted #03a4fb;
}

pre {
    background-color: black !important;
    border:none !important;
    border-radius: 0px !important;
    font-size: $base-font-size !important;
    padding: 1.5em !important;
    color:white !important;
}

em {
    color: lighten($text-color, 20%);
}

hr {
    background-color: white;
}

code {
    // background-color: black !important;
    // color: white !important;
    border: none !important;
    border-radius: 0px !important;
}

p {
    text-align: justify;
    font-size: $base-font-size !important;
}

nav {
    font-family: $nav-font-family;
}


/* Navigation bar */
/*///////////////////////////////////////////////////////////////////////*/
.navbar {
    box-shadow: 0px 1px 10px 0px rgba(0, 0, 0, 0.15);
}

.navbar-header {
    float: right
}

.fa {
    color: white;
}

.navbar-default {
    background: $background-color !important;
    // background: #093750 !important;
    border: none !important;

}

.navbar-default .navbar-nav > li > a {
    color:$text-color !important;
}
.navbar-default .navbar-nav > li > a:hover {
    color:$interact-color !important;

}
.navbar-default .navbar-nav > li > a:focus {
    color:$interact-color !important;
}
.navbar-default .navbar-brand {
    color:$text-color !important;
}

/* Jumbotron */
/*///////////////////////////////////////////////////////////////////////*/
.jumbotron {
    background: $background-color !important;
    box-shadow: 0px 1px 10px 0px rgba(0, 0, 0, 0.15);
}
.tagline {
    float: right;
    font-size: $base-font-size * 0.6 !important;
    text-align: justify;
}

// Landing page
// ////////////////////////////////////////////////////////////////////////
.col {
    font-family: $bulletin-font-family;
}
.col p {
    font-family: $base-font-family;
}
.frame {
    border-style: solid;
    border-width: 5px;
    border-color: white;
    margin: 20px;
    padding: 20px;
}

.row h2 {
    text-align: center;
}

/* Footer */
/*///////////////////////////////////////////////////////////////////////*/
.footer-content {
    text-align: center;
    padding: 4em;
}

/* Body */
/*///////////////////////////////////////////////////////////////////////*/
body {
    background-color: $background-color !important;
    // background-color: #022335 !important;
    font-family: $base-font-family;
    color: $text-color !important;
    padding-top: 70px;
}
h1, h2, h3, h4 {
    font-family: $headline-font-family;
}
body h1, body h2, body h3, body h4 {
    font-weight: bold;
}
body h2, body h3, body h4 {
    padding-bottom: 1em;
    padding-top: 1em;
}

.content {
    max-width: 800px;
}

.double-col {
    max-width: 100% !important;
}

/* Post */
/*///////////////////////////////////////////////////////////////////////*/
.excerpt-list {
    text-align: left;
}
.excerpt-box {
    padding-top: 2em;
    padding-bottom: 2em;
}

.excerpt-title {
    font-size: $huge-font-size !important;
    color:  $title-color !important;
    font-weight: bold;
    text-align: center;
}

.excerpt-content {
    color: $text-color !important;
    text-align: justify;
    padding-top: 0.5em;
    padding-bottom: 0.3em;
}

/* Post and page. */
/*///////////////////////////////////////////////////////////////////////*/
.page-title {
    text-align: center;
    color: $title-color !important;
}
.page-tagline {
    font-size: $base-font-size !important;
    text-align: center;
}
.post-content {
    font-size: $base-font-size;
    text-align: justify;
    padding-bottom: 5em;
}
.post-meta {
    color: skyblue !important;
}

.pagination {
    width: 100%;
    text-align: center;
  a {
      font-size: $large-font-size;
      color: $link-color;
      &[href*=archive] {
          &:before, &:after { content: '\2014'; padding: 0 .3em; }
    }
  }
}
.paper-title {
    font-size:150%;
    font-family: $nav-content-font-family;
}

#archive-title {
    color: $title-color
}

#markdown-toc {
    margin: auto;
    width: 50%;
}

/* Images */
/*///////////////////////////////////////////////////////////////////////*/
#floated {
    float: left;
    margin: 0 15px;
    /* width: 150px; */
}
#cloud {
    text-align: center;
    color: white;
    margin: 15px;
}

.illustration {
    text-align: center;
    color: white;
    margin: 15px;
    padding: 15px;
}
.nav-box {
    box-shadow: 10px 10px 5px #fff;
}

/* Content */
/*///////////////////////////////////////////////////////////////////////*/
.container .emph {
    color: red;
}

/* Utilities */
/*///////////////////////////////////////////////////////////////////////*/
// Use media queries like this:
@mixin media-query($device) {
    @media screen and (max-width: $device) {
        @content;
    }
}

// Import partials from `sass_dir` (defaults to `_sass`)
/*///////////////////////////////////////////////////////////////////////*/
@import "syntax-highlighting";
